import { useRpcWorker } from '@/hook/useRpcWorker'
import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import { CommonTypeInfo, RpcNode, ToastType, TransactionStatusType } from '@cetus/types'
import { CheckBox } from '@cetus/ui-kit'
import { isAvailableObject } from '@cetus/utils'
import { Box, Button, HStack, Input, InputGroup, InputRightAddon, Text, VStack } from '@chakra-ui/react'
import { useCallback, useMemo, useState } from 'react'
import { useGlobalToast } from '../../../hooks'

const RPCContent = ({ closeModal }: { closeModal: () => void }) => {
  useRpcWorker()
  const { rpcSpeed } = useRpcConfigStore()
  const regTest = (val: any) => {
    const reg = /^(http|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/
    return !reg.test(val)
  }
  const { nodes, currentNode, switchNode } = useRpcConfigStore()
  const [isCheckCustom, setIsCheckCustom] = useState(currentNode?.name == 'Custom')
  const [inputValue, setInputValue] = useState(currentNode?.name == 'Custom' ? currentNode?.link : '')
  const nodeList = useMemo(() => {
    return nodes
  }, [nodes])
  const { successTsToast } = useGlobalToast()
  const handleChangeNode = (item: RpcNode) => {
    console.log('üöÄ ~ handleChangeNode ~ item:', item)
    if (item.name !== 'Custom') {
      setInputValue('')
      setIsCheckCustom(false)
    }
    switchNode(item)
    // closeModal()
    const info: ToastType = {
      linkLabel: '',
      getShowInfo: (status: TransactionStatusType): CommonTypeInfo => {
        const info: CommonTypeInfo = {
          toastTitleText: `RPC update`,
          toastDescriptionContent: () => (
            <Text fontSize="14px" color="primary_gray">
              {`RPC is currently set to `}
              <Text as="span" fontSize="14px" color="primary_gray" wordBreak="break-all">
                {item.name !== 'Custom' ? item.name : item.link}
              </Text>
            </Text>
          )
        }
        return info
      }
    }
    successTsToast(info)
  }

  const rpcSpeedColor = useCallback((speed: number) => {
    if (speed === 0) {
      return 'text_paragraph'
    }

    if (speed < 100) {
      return 'primary_green'
    }

    if (speed > 200) {
      return 'primary_red'
    }

    return 'primary_yellow'
  }, [])

  return (
    <Box w="100%" mt="-16px">
      {nodeList.map(item => {
        const speed = Number(rpcSpeed[item?.link] || 0)
        return (
          <VStack mt="16px" key={item?.link} align="flex-start">
            {item?.name !== 'Custom' && (
              <HStack w="100%" justifyContent="space-between">
                <HStack w="100%" onClick={() => handleChangeNode(item)} _hover={{ p: { color: 'primary' } }}>
                  <CheckBox checked={currentNode.name === item?.name} onClick={() => {}} />
                  <Text
                    cursor="pointer"
                    fontSize={{ base: '16px', lg: '14px' }}
                    color={currentNode.name === item?.name ? 'primary' : 'text_paragraph'}
                  >
                    {item?.name}
                  </Text>
                </HStack>
                <HStack gap="5px">
                  {speed > 0 && <Box w="6px" h="6px" bg={rpcSpeedColor(speed)} borderRadius="50%" />}
                  <Text fontSize="12px" fontWeight="500" color={rpcSpeedColor(speed)}>
                    {rpcSpeed[item.link]}
                    {isAvailableObject(rpcSpeed) ? 'ms' : ''}
                  </Text>
                </HStack>
              </HStack>
            )}
          </VStack>
        )
      })}
      <HStack w="100%" mt="16px" _hover={{ p: { color: 'primary' } }} onClick={() => setIsCheckCustom(!isCheckCustom)}>
        <CheckBox checked={currentNode.name === 'Custom'} onClick={() => {}} />
        <Text cursor="pointer" fontSize={{ base: '16px', lg: '14px' }} color={currentNode.name === 'Custom' ? 'primary' : 'text_paragraph'}>
          Custom RPC URL
        </Text>
      </HStack>
      {(currentNode.name === 'Custom' || isCheckCustom) && (
        <InputGroup mt="16px" h="40px" borderRadius="12px" border="1px solid" borderColor="border">
          <Input
            p="13px 16px"
            fontSize={{ base: '16px', lg: '14px' }}
            placeholder="Custom RPC URL"
            value={inputValue}
            onChange={e => setInputValue(e.target.value)}
          />
          <InputRightAddon pr="4px">
            <Button
              disabled={regTest(inputValue)}
              fontSize="14px"
              fontWeight="500"
              h="32px"
              w="78px"
              borderRadius="8px"
              onClick={() => {
                handleChangeNode({ name: 'Custom', link: inputValue })
              }}
            >
              Save
            </Button>
          </InputRightAddon>
        </InputGroup>
      )}
    </Box>
  )
}

export default RPCContent
