import useWebConfigStore from '@cetus/stores/src/useWebConfigStore'
import { HStack, Image, Switch, Text } from '@chakra-ui/react'

type SwapWidgetDisplayProps = {
  h: string
  icon: string
  borderRadius?: string
  color?: string
}

export function SwapWidgetDisplay(props: SwapWidgetDisplayProps) {
  const { h, icon, borderRadius = '12px', color = 'text_caption' } = props
  const { isSwapWidgetDisplay, setIsSwapWidgetDisplay } = useWebConfigStore()
  return (
    <HStack
      w="100%"
      pl="16px"
      pr="16px"
      h={h}
      border="1px solid"
      borderColor="border"
      bg="menu_item_bg"
      justifyContent="center"
      gap="24px"
      borderRadius={borderRadius}
    >
      <HStack w="100%" gap="8px">
        <Image
          draggable="false"
          src={icon}
          fallbackSrc="/images/placeholder-token@2x.png"
          w="24px"
          h="24px"
          pointerEvents="none" // Prevent click events on the image
        />
        <Text color={color} fontSize="14px">
          Display swap widget
        </Text>
      </HStack>

      <Switch isChecked={isSwapWidgetDisplay} onChange={() => setIsSwapWidgetDisplay(!isSwapWidgetDisplay)} />
    </HStack>
  )
}
