import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import { CommonTypeInfo, ExplorerItem, ToastType, TransactionStatusType } from '@cetus/types'
import { CheckBox } from '@cetus/ui-kit'
import { HStack, Text, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'
import { useGlobalToast } from '../../../hooks'
import ListItem from './ListItem'

const ExplorerContent = ({ closeModal, isH5 }: { closeModal: () => void; isH5?: boolean }) => {
  const { explorerList, currentExplorer, switchExplorer } = useRpcConfigStore()
  const list = useMemo(() => {
    console.log('üöÄ ~ list ~ explorerList:', explorerList)
    return explorerList
  }, [explorerList])
  const { successTsToast } = useGlobalToast()
  const handleChangExplorer = (item: ExplorerItem) => {
    switchExplorer(item)
    closeModal()
    const info: ToastType = {
      linkLabel: '',
      getShowInfo: (status: TransactionStatusType): CommonTypeInfo => {
        const info: CommonTypeInfo = {
          toastTitleText: `Explorer update`,
          toastDescriptionContent: () => (
            <Text fontSize="14px" color="primary_gray">
              {`Explorer is currently set to `}
              <Text as="span" fontSize="14px" color="primary_gray" wordBreak="break-all">
                {item.name}
              </Text>
            </Text>
          )
        }
        return info
      }
    }
    successTsToast(info)
  }
  return (
    <>
      {!isH5 ? (
        <VStack w="100%">
          {list.map((item: ExplorerItem) => {
            return (
              <ListItem
                key={item.link}
                label={item.name}
                value={item.link}
                isActive={item.link === currentExplorer.link}
                onClick={() => handleChangExplorer(item)}
              />
            )
          })}
        </VStack>
      ) : (
        <VStack gap="16px" mb="16px">
          {list.map(item => {
            return (
              <HStack w="100%" key={item.link}>
                <CheckBox checked={item.link === currentExplorer.link} onClick={() => handleChangExplorer(item)} />
                <Text fontSize={{ base: '16px', lg: '14px' }}>{item.name}</Text>
              </HStack>
            )
          })}
        </VStack>
      )}
    </>
  )
}

export default ExplorerContent
