import { Icon } from '@cetus/ui-kit'
import {
  Box,
  HStack,
  Popover,
  PopoverBody,
  PopoverCloseButton,
  PopoverContent,
  PopoverHeader,
  PopoverTrigger,
  Text,
  useDisclosure,
  VStack
} from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import { SwapWidgetDisplay } from '../SwapWidgetDisplay'
import { SettingProps } from '../type'
import PCPreferred from './Preferred'
import PCRPCNode from './RPCNode'
interface PCSettings extends SettingProps {
  isCetusPump: boolean
}
const PCSettings = ({ swapWidgetDisplay, preferred, rpc }: PCSettings) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  const [title, setTitle] = useState('Settings')
  const showMainSetting = useMemo(() => title === 'Settings', [title])
  const handleTitleChange = (value: string) => {
    setTitle(value)
  }

  const onBackMainSetting = () => {
    setTitle('Settings')
  }
  return (
    <Popover isLazy isOpen={isOpen} onOpen={onOpen} placement="bottom-end" onClose={onClose}>
      <PopoverTrigger>
        <Box
          bg="bg_secondary"
          w="36px"
          h="36px"
          p="7px"
          border="1px solid"
          borderColor="border"
          borderRadius="8px"
          cursor="pointer"
          _hover={{
            bg: 'button_outline_hov_bg',
            svg: {
              fill: 'text_caption'
            }
          }}
        >
          <Icon xlinkHref="#icon-icon_settings1" variant="gray" />
        </Box>
      </PopoverTrigger>
      <PopoverContent minW="360px" p="0px 16px 16px" bg="bg_secondary" borderRadius="12px" maxH="calc(100vh - 80px)" overflowY="auto">
        <PopoverHeader p="0px" fontWeight="500" h="56px" lineHeight="56px" border="none">
          <HStack>
            {!showMainSetting && (
              <Icon xlinkHref="#icon-icon_descending" transform="rotate(90deg)" svgFill="text_caption" fontSize="16px" onClick={onBackMainSetting} />
            )}
            <Text color="text_caption" fontSize="16px">
              {title}
            </Text>
          </HStack>
        </PopoverHeader>
        {!showMainSetting && <PopoverCloseButton h="50px" onClick={onBackMainSetting} />}

        <PopoverBody p="0px">
          <VStack>
            {showMainSetting && swapWidgetDisplay && <SwapWidgetDisplay icon={swapWidgetDisplay.icon} h="42px" />}
            {preferred && (
              <PCPreferred
                onShowOptions={() => handleTitleChange('Preferred')}
                showOptions={title === 'Preferred'}
                showMainSetting={showMainSetting}
                closeModal={onBackMainSetting}
              />
            )}
            {rpc && <PCRPCNode showMainSetting={showMainSetting} closeModal={onClose} />}
          </VStack>
        </PopoverBody>
      </PopoverContent>
    </Popover>
  )
}

export default PCSettings
