import { Icon } from '@cetus/ui-kit'
import { HStack, Text, VStack } from '@chakra-ui/react'
import { ISettingItemProps } from '../type'

const PCSettingItem = ({ icon, label, ...rest }: ISettingItemProps) => {
  return (
    <VStack w="100%" p="16px" borderRadius="12px" border="1px solid" borderColor="border" bg="menu_item_bg" justify="flex-start" gap="24px">
      <HStack w="100%" gap="8px">
        <Icon xlinkHref={icon} fontSize="20px" />
        <Text fontSize="14px">{label}</Text>
      </HStack>
      {'children' in rest ? (
        rest?.children
      ) : (
        <HStack w="100%" justify="space-between" cursor="pointer" onClick={rest.onClick}>
          <Text color="text_caption" fontSize="14px">
            {rest.value}
          </Text>
          <Icon transform="rotate(270deg)" xlinkHref="#icon-icon_arrow" fontSize="12px" variant="gray" />
        </HStack>
      )}
    </VStack>
  )
}

export default PCSettingItem
