import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import ExplorerContent from '../ExplorerContent'
import { IPCtemSettingInterface } from '../type'
import PCSettingItem from './Item'

interface PCPreferredProps extends IPCtemSettingInterface {
  closeModal: () => void
}

const PCPreferred = ({ onShowOptions, showOptions, showMainSetting, closeModal }: PCPreferredProps) => {
  const { currentExplorer } = useRpcConfigStore()
  return showOptions ? (
    <ExplorerContent closeModal={closeModal} />
  ) : (
    showMainSetting && (
      <PCSettingItem icon="#icon-icon_preferredexplorer" label="Preferred Explorer" value={currentExplorer?.name} onClick={onShowOptions} />
    )
  )
}

export default PCPreferred
