import { Icon } from '@cetus/ui-kit'
import { Box, Flex } from '@chakra-ui/react'
import { lazy, Suspense, useState } from 'react'

import Notifications from '../Notifications'
import { UserActionProps } from '../type'
const H5Menus = lazy(() => import('../menus/H5Menus'))
const ConnectButton = lazy(() => import('@/components/wallet/ConnectButton'))

const H5Actions = ({ menus, logo, setting, notifications, jump, isCetusPump, children }: UserActionProps) => {
  const [menuOpen, setMenuOpen] = useState<boolean>(false)
  return (
    <>
      <Flex align="center" gap="8px">
        {/* {chain && <SwitchChain {...chain} />} */}
        {/* {isCetusPump && <BackButton w="114px" h="36px" text="Back to DEX" onClick={() => jump('/')} />} */}
        <Suspense fallback={<div />}>
          <ConnectButton isCetusPump={isCetusPump} />
        </Suspense>
        {notifications && <Notifications />}
        <Box
          w="36px"
          h="36px"
          p="7px"
          bg="bg_secondary"
          borderRadius="8px"
          border="1px solid"
          borderColor="border"
          cursor="pointer"
          _hover={{ bg: 'button_outline_hov_bg' }}
        >
          <Icon xlinkHref="#icon-icon_menu" onClick={() => setMenuOpen(!menuOpen)} />
        </Box>
      </Flex>
      <H5Menus
        menus={menus!}
        logo={logo!}
        setting={setting}
        jump={jump!}
        isCetusPump={isCetusPump}
        isOpen={menuOpen}
        onClose={() => setMenuOpen(false)}
      >
        {children}
      </H5Menus>
    </>
  )
}

export default H5Actions
