import { useInterval } from '@cetus/hooks'
import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import { defaultNodes } from '@cetus/types'
import { useEffect } from 'react'

export function useRpcWorker() {
  let worker: Worker | null = null
  const { setRpcSpeed } = useRpcConfigStore()

  useInterval({
    interval: 5 * 1000,
    callback: () => {
      startTestRpc()
    }
  })

  const startTestRpc = () => {
    if (worker === null) {
      worker = new Worker('/worker/rpc.worker.js')
    }
    worker.postMessage({
      rpcList: defaultNodes.filter(rpc => rpc.link.length > 0).map(rpc => rpc.link)
    })

    worker.onmessage = event => {
      if (event.data.error) {
        console.log('üöÄ ~ file: useRpcWorker.ts:15 ~ useEffect ~ event.data.error:', event.data.error)
        setRpcSpeed({})
      } else {
        console.log('üöÄ ~ file: useRpcWorker.ts:15 ~ useEffect ~ event.data:', event.data)
        if (event.data) {
          setRpcSpeed(event.data)
        } else {
          setRpcSpeed({})
        }
      }
    }
  }

  useEffect(() => {
    startTestRpc()

    return () => {
      if (worker) {
        worker.terminate()
      }
    }
  }, [])
}
